*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body
{
    overflow: hidden;
}
@import url(http://fonts.googleapis.com/css?family=Lato:900);
body {
    background-color: #24282f;
    margin: 0;
    padding: 0;
}
canvas {
    position: absolute;
    top: 0;
    left: 0;
}
  #canvas-number {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }
.experience
{
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    z-index: -10;

}
#firework{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}
#canvas1{
    z-index: -100;
}
.z1{
    z-index: 1 !important;
}
.z{
    z-index: 10;

}
.credits
{
    position: absolute;
    bottom: 20px;
    right: 20px;
    text-align: right;
    font-family: 'Roboto', sans-serif;
    color: #ff8557;
    cursor: pointer;
    padding: 0 10px;
    animation: bb 2s linear 0s infinite;
    font-weight: bold;
}
@keyframes bb {
    0% {
        transform: translate(0,0);
        opacity: 0;
    }
    
    20% {
        transform: translate(0,3px);
        opacity: 1;
    }
    80% {
        transform: translate(0,10px);
        opacity: 1;
    }
    90% {
        transform: translate(0,10px);
        opacity: 0;
    }
    100% {
        transform: translate(0,10px);
        opacity: 0;
    }
}
.credits a
{
    color: inherit;
}

